<template>
  <div class="head-container">
    <div class="head-content-big">
      <div class="left">
        Reaper
      </div>
      <div class="search" @keyup.enter="search">
        <el-input v-show="show" placeholder="请输入内容" v-model="search_content" class="input-with-select">
          <el-select v-model="select" slot="prepend" placeholder="请选择搜索类别">
            <el-option label="域名" value="域名"></el-option>
            <el-option label="ip" value="ip"></el-option>
            <el-option label="port" value="port"></el-option>
            <el-option label="check" value="check"></el-option>
            <el-option label="url" value="url"></el-option>
            <el-option label="cors" value="cors"></el-option>
          </el-select>
          <!-- <el-button slot="append" icon="el-icon-search" @click="search"></el-button> -->
        </el-input>
      </div>
      <div class="right">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          text-color="#fff">
          <el-menu-item index="1">主 页</el-menu-item>
          <el-menu-item index="2">简 介</el-menu-item>
          <el-menu-item index="3">关 于 我 们</el-menu-item>
        </el-menu>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      activeIndex: '1',
      select:'域名',
      search_content:'',
      show:true
    }
  },
  mounted(){
  },
  methods: {
    search(){
      this.$emit('search', this.select,this.search_content);
    },
    handleSelect(key, keyPath) {
      if(key == 1){
        this.activeIndex = '1'
        this.$emit('exchange', 1);
        this.show = true
      }
      else if(key == 2){
        this.activeIndex = '2'
        this.$emit('exchange', 2);
        this.show = false
      }
      else if(key == 3){
        this.activeIndex = '3'
        this.$emit('exchange', 3);
        this.show = false
      }
    }
  }
}
</script>

<style lang="scss">
.head-container{
  .el-select .el-input {
    width: 90px;
    font-family: "PingFang SC","微软雅黑",Arial,sans-serif;
  }
}
.head-content-big{
  .el-menu.el-menu--horizontal{
    border:0;
  }
  .el-menu{
    background-color: transparent;
  }
  .el-menu--horizontal>.el-menu-item.is-active{
    border-bottom: 2px solid #ffd04b;
    color: #ffd04b;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .el-menu--horizontal>.el-menu-item:not(.is-disabled):focus, 
  .el-menu--horizontal>.el-menu-item:not(.is-disabled):hover, 
  .el-menu--horizontal>.el-submenu .el-submenu__title:hover{
    background-color: rgba(0, 0, 0, 0.2);
    color: #ffd04b;
  }
  .el-menu--horizontal>.el-menu-item{
    font-size: 16px;
    height: 80px;
    width: 120px;
    text-align: center;
    line-height: 80px;
  }
}
</style>
<style lang="scss" scoped>
.head-container{
  font-family: "PingFang SC","微软雅黑",Arial,sans-serif;
  width: 100%;
  height: 80px;
  background: linear-gradient(45deg, #0ebeff, #158a6d);
  position: fixed;
  //box-shadow: 10px 10px 5px rgba(0,0,0,0.1);
  box-shadow: 5px 5px 15px 2px rgba(0,0,0,0.2);
  z-index: 99;
  .head-content-big{
    margin: 0 auto;
    width: 1200px;
    height: 100%;
    .left{
      height: 100%;
      color: white;
      font-size: 45px;
      font-weight: bold;
      line-height: 80px;
      float: left;
    }
    .right{
      margin-left: 50px;
      height: 100%;
      float: left;
    }
    .search{
      margin-left: 240px;
      float: left;
      width: 350px;
      height: 100%;
      line-height: 80px;
    }
  }
}
</style>


