<template>
  <div class="data-container">
    <head1 @search="search1" @exchange="exchange"></head1>
    <div v-if="home" class="data-content" ref="page">
      <div style="width:100px;height:100px;"></div>
      <div
        style="width:100%;height:100%"
        v-loading="loading"
        element-loading-text="拼命加载中">
        <div class="left" :style="leftStyleObj">
          <div class="title">
            域 名
          </div>
          <div 
            v-for="(item,index) in list" :key="index" 
            class="left-item" 
            @click="selectId(index)"
            @mouseover="change(index)"
            @mouseout="out(index)"
            :class="{'leftclick':index==selectedId,'lefthover':index==hoverId}">
            {{ item }}
          </div>
        </div>
        <div class="right" :style="rightStyleObj">
          <div class="select-url">
            <div style="float:left;width:200px;" @keyup.enter="selectUrlBySize">
              <el-input placeholder="根据输入size过滤url" v-model="dsize">
              </el-input>
            </div>
            <div style="float:left;width:200px;margin-left:30px;" @keyup.enter="selectUrlByLine">
              <el-input placeholder="根据输入line过滤url" v-model="dline">
              </el-input>
            </div>
            <div style="float:left;margin-left:30px;">
              <el-button type="primary" @click="clearCondition" round>刷新</el-button>
            </div>
          </div>
          <div class="tree">
            <el-tree
              icon-class="el-icon-d-caret"
              class="filter-tree"
              :data="treedata"
              :props="defaultProps"
              default-expand-all
              ref="tree">
              <span class="span-ellipsis" slot-scope="{ node }">
                <span :title="node.label">{{ node.label }}</span>
              </span>
            </el-tree>
          </div>
        </div>
      </div>
    </div>
    <div v-if="intro" >
      <introduction></introduction>
    </div>
    <div v-if="about1">
      <about></about>
    </div>
  </div>
</template>

<script>
import head1 from './head'
import introduction from './introduction'
import about from './about'
export default {
  data(){
    return {
      dline:null,
      isSelectUrlBySize:false,
      isSelectUrlByLine:false,
      loading: true,
      filename:[],
      home:true,
      intro:false,
      about1:false,
      select:'域名',
      search_content:'',
      isShow:null,
      data:null,
      info:{},
      hoverId:0,
      selectedId:0,
      list:[],                  //域名集合
      clientHeight:'',
      dsize:null,
      maxSize:null,
      leftStyleObj:{height:''},
      rightStyleObj:{height:''},
      treedata: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    }
  },
  components: {head1,about,introduction},
  created(){
    window.addEventListener('resize', this.getHeight);
    this.getHeight()
  },
  mounted(){
    this.clientHeight = `${document.documentElement.clientHeight}`;//获取浏览器可视区域高度
    let clientWidth = `${document.documentElement.clientWidth}`
    if(parseInt(clientWidth) > 1100)
      this.isShow = true
    else
      this.isShow = false
    let that = this;
    window.onresize = function(){
      this.clientHeight =  `${document.documentElement.clientHeight}`;
      if(that.$refs.page){
          that.$refs.page.style.height = clientHeight - 100 + 'px';
      }
    }
    this.$axios.get('http://localhost:10502/getFile')
    .then(res => {
      this.data = res.data
      this.parent() 
      this.getList()
      this.loading = false
      if(this.selectedId != null)
        this.selectId(this.selectedId)
      console.log(this.treedata)
    })
    .catch(error => {
      console.log(error);
      this.loading = false
    });
  },
  watch:{
    clientHeight(){   
       this.changeFixed(this.clientHeight) 
    },
    ddd(){
      console.log(this.ddd)
    }
  },
  destroyed(){
    window.removeEventListener('resize', this.getHeight)
  },
  methods: {
    exchange(index){
      //console.log(index)
      if(index == 1){
        this.home = true
        this.intro = false
        this.about1 = false
      }else if(index == 2){
        this.home = false
        this.intro = true        
        this.about1 = false
      }else if(index == 3){
        this.home = false
        this.intro = false
        this.about1 = true
      }
    },
    //清除条件
    clearCondition(){
      this.isSelectUrlByLine = false
      this.isSelectUrlBySize = false
      this.dsize = null
      this.dline = null
      this.selectId(this.selectedId)
    },
    //清除指定size
    selectUrlBySize(){
      //console.log(this.treedata[0].children[3])
      if(!this.isSelectUrlByLine)
        this.selectId(this.selectedId)
      this.isSelectUrlBySize = true
      let data = this.treedata[0].children[3].children
      let u = null
      let url = []
      for(let i in data){
        u = data[i]["label"].split("{")
        u = u[u.length - 1].trim()
        u = u.slice(0,u.length - 1).split(":")
        u = u[u.length - 2].split(",")
        u = parseInt(u[0])
        if(u != parseInt(this.dsize))
          url.push(data[i])
      }
      this.treedata[0].children[3].children = url
    },
    selectUrlByLine(){
      if(!this.isSelectUrlBySize)
        this.selectId(this.selectedId)
      this.isSelectUrlByLine = true
      let data = this.treedata[0].children[3].children
      let u = null
      let url = []
      for(let i in data){
        u = data[i]["label"].split("{")
        u = u[u.length - 1].trim()
        u = u.slice(0,u.length - 1).split(":")
        u = parseInt(u[u.length - 1])
        if(u != parseInt(this.dline))
          url.push(data[i])
      }
      this.treedata[0].children[3].children = url
    },
    out(index){
      this.hoverId = null
    },
    change(index){
      this.hoverId = index
    },
    selectId(index){
      this.selectedId= index
      this.searchYm(this.list[index])
    },
    getHeight(){
      this.leftStyleObj.height=window.innerHeight-100+'px';
      this.rightStyleObj.height=window.innerHeight-100+'px';
    },
    searchYm(ym){
      this.parent()
      //移除内容区除了该选中域名之外的所有内容
      for(let index = this.treedata.length - 1; index >= 0; index--){
        if(this.treedata[index]["label"] != ym){
          this.treedata.splice(index,1); 
        }
      }
    },
    search1(a,b){
      this.select = a
      this.search_content = b
      this.parent()
      this.getList()
      let l = []
      if(this.search_content == ''){
        if(this.list.length > 0)
          this.selectId(0)
        this.loading = false
        return
      }
      if(this.select === '域名'){
        for(let index = this.treedata.length - 1; index >= 0; index--){
          if(this.treedata[index]["label"].indexOf(this.search_content) == -1){
            this.treedata.splice(index,1); 
          }
        }
        for(let i = this.list.length - 1; i >= 0; i--){
          if(this.list[i].indexOf(this.search_content) == -1){
            this.list.splice(i,1); 
          }
        }
        l = this.list
      }else if(this.select === 'ip'){
        l = []
        for(let index = this.treedata.length - 1; index >= 0; index--){
          let c = this.treedata[index].children
          if((c[0].children[0])["label"].indexOf(this.search_content) == -1){
            this.treedata.splice(index,1) 
          }else{
            l.unshift(this.treedata[index]["label"])
          }
        }
        this.list = l
      }else if(this.select === 'port'){
        l = []
        for(let index = this.treedata.length - 1; index >= 0; index--){
          let flag = false
          let c = this.treedata[index].children
          for(let j in c[1].children){
            if((c[1].children[j])["label"].indexOf(this.search_content) != -1){
              flag = true
              break;
            }
          }
          if(!flag)
            this.treedata.splice(index,1)
          else
            l.unshift(this.treedata[index]["label"])
        }
        this.list = l
      }else if(this.select === 'check'){
        l = []
        for(let index = this.treedata.length - 1; index >= 0; index--){
          let flag = false
          let c = this.treedata[index].children
          for(let j in c[2].children){
            if((c[2].children[j])["label"].indexOf(this.search_content) != -1){
              flag = true
              break;
            }
          }
          if(!flag)
            this.treedata.splice(index,1)
          else
            l.unshift(this.treedata[index]["label"])
        }
        this.list = l
      }else if(this.select === 'url'){
        l = []
        for(let index = this.treedata.length - 1; index >= 0; index--){
          let flag = false
          let c = this.treedata[index].children
          for(let j in c[3].children){
            if((c[3].children[j])["label"].indexOf(this.search_content) != -1){
              flag = true
              break;
            }
          }
          if(!flag)
            this.treedata.splice(index,1)
          else
            l.unshift(this.treedata[index]["label"])
        }
        this.list = l
      }else if(this.select === 'cors'){
        l = []
        for(let index = this.treedata.length - 1; index >= 0; index--){
          let flag = false  
          let c = this.treedata[index].children
          for(let j in c[4].children){
            if((c[4].children[j])["label"].indexOf(this.search_content) != -1){
              flag = true
              break;
            }
            for(let k in (c[4].children[j])["children"]){
              if((c[4].children[j])["children"][k]["label"].indexOf(this.search_content) != -1){
                flag = true
                break;
              }
            } 
            if(flag)
              break;
          }
          if(!flag)
            this.treedata.splice(index,1)
          else
            l.unshift(this.treedata[index]["label"])
        }
        this.list = l
      }
      if(l != [])
        this.selectId(0)
    },
    changeFixed(clientHeight){
　　　　if(this.$refs.page){
　　　　　　this.$refs.page.style.height = clientHeight - 100 + 'px';
　　　　}
　　},
    getList(){
      this.list = []
      for(let i in this.data)
        this.list.push(i)
    },
    parent(){
      let parent = [];
      for(let i in this.data){
        let obj = {
          label: i,
          children: []
        }
        parent.push(obj)
      }
      //console.log(parent)
      this.children(parent);
    },
    children(parent){
      for(let i in this.data){
        for(let j in parent){
          if(parent[j]["label"] === i){
            //console.log(this.data[i])
            //console.log(this.data[i]["ip"])
            let port_children = []
            let check_children = []
            let url_children = []
            let cors_children = []
            let data_port = this.data[i]["port"]
            let data_check = this.data[i]["check"]
            let data_url = this.data[i]["url"]
            let data_cors = this.data[i]["cors"]
            for(let port in data_port){
              let p = {
                "label":port + " : " + data_port[port]
              }
              //console.log(port)
              port_children.push(p)
            }
            for(let check in data_check){
              let c = {
                "label":check + " : " + data_check[check]
              }
              //console.log(port)
              check_children.push(c)
            }
            for(let url in data_url){
              let u = {
                "label":url + " : " + JSON.stringify(data_url[url])
              }
              //console.log(port)
              url_children.push(u)
            }
            for(let cors in data_cors){
              let cors_children_children = []
              if(data_cors[cors] != ''){
                let d = data_cors[cors].slice(1,data_cors[cors].length-1).split(',')
                for(let l in d){
                  let cr2 = {
                    "label": d[l]
                  }
                  cors_children_children.push(cr2)
                }
              }
              let cr = {
                label: cors,
                children: cors_children_children
              }
              cors_children.push(cr)
            }
            let children = [{
              label: 'ip',
              children: [{
                label: this.data[i]["ip"]
              }]
            },{
              label: 'port',
              children: port_children
            },{
              label: 'check',
              children: check_children
            },{
              label: 'url',
              children: url_children
            },{
              label: 'cors',
              children: cors_children
            }]
            //console.log(children)
            parent[j].children = children
          }
        }
      }
      //console.log(parent)
      this.treedata = parent
    },
    handleNodeClick(data) {
      console.log(data);
    }
  }
}
</script>

<style lang="scss">
.data-content{
  .el-tree-node__content{
    height: 30px;
  }
  .el-tree-node__label{
    font-size: 16px;
  }
  .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
  .right{
    el-input{
      float:left;
      width: 80%;
    }
  }
  .el-input__inner{
    border-radius: 25px;
  }
}
.data-content .el-select .el-input{
  width: 100px;
}
</style>

<style lang="scss" scoped>
.data-container{
  font-family: "PingFang SC","微软雅黑",Arial,sans-serif;
  margin: 0 auto;
  //padding: 10px 0px 0px 15px;
  padding: 0;
  width: 100%;
  background-color: #fff;
  .data-content{
    margin: 0 auto;
    //overflow: hidden;
    width: 1200px;
    .left{
      overflow: scroll;
      margin-left: 20px;
      width: 230px;
      float: left;
      .title{
        font-size: 18px;
        font-weight: bold;
        line-height: 50px;
      }
      .left-item{
        cursor: pointer;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
      }
      .lefthover{
        color: #409EFF;
      }
      .leftclick{
        color: #409EFF;
      }
    }
    .right{
      overflow: scroll;
      width: 945px;
      float: left;
      .select-url{
        height:50px;
        margin-top:10px;
        //width: 500px;
        margin-left: 25px;
      }
      .tree{
        margin-left: 20px;
        margin-top: 0px;
        .span-ellipsis {
          width: 100%;
          font-size: 15px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }  
  }
}
</style>


