<template>
  <div class="intro-container">
    <div style="width:100px;height:100px;"></div>
    <div class="intro-content">
      <div class="title">
        作 品 介 绍
      </div>
      <div class="content">
        <p>
          随着计算机网络信息技术的飞跃发展，网络安全问题也随之而来。Web渗透测试是网络信息安全防御的重要手段，而信息收集是其中非常关键
          的一环，其完整性会直接影响渗透测试的成功率。
        </p>
        <p>
          本作品是一款全面的渗透测试信息收集工具，可以实现自动化地收集目标Web应用的各种信息，主要通过三种技术来实现作品功能，
          分别是：子域名获取、端口服务收集以及目录扫描。首先在确定目标系统后，通过网络爬虫、字典暴力枚举等手段收集子域名，并对
          泛解析等进行处理。与此同时，使用多线程以及协程技术充分利用系统资源进行加速。其次，我们对域名以及ip地址的端口进行扫描，
          并对Web端口以及其他脆弱端口分别进行CORS跨域和未授权访问的漏洞检查，进一步了解Web服务器的网站架构部署信息。然后，针对
          所有目标进行敏感目录和敏感信息的扫描，进一步完善资产清单，为后续人工渗透节约大量时间。除了文本资产以外，为了加强工具的
          易查阅性，资产清单将以模块的形式放在html页面中，同时支持用户进行静态查询。
        </p>
        <p>
          在渗透测试的前置工作中，原本需要手工收集各类目标信息，工作较为繁琐且针对性不强。本作品实现了信息收集自动化，有效地节约
          了渗透测试人工花费时间成本，提高测试效率，且工作高效简洁。
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
    }
  },
  mounted(){
    
  },
  watch:{
    
  },
  methods:{

  }
}
</script>

<style lang="scss" scoped>
.intro-container{
  margin: 0 auto;
  //padding: 10px 0px 0px 15px;
  padding: 0;
  width: 1200px;
  background-color: #fff;
  font-family: "PingFang SC","微软雅黑",Arial,sans-serif;
  .intro-content{
    overflow: scroll;
    width: 100%;
    .title{
      font-size: 30px;
      margin: 0 auto;
      width: 70%;
      height: 100px;
      line-height: 100px;
      text-align: center;
    }
    .content{
      width: 70%;
      margin: 0 auto;
      p {
        // text-align: justify;
        // text-justify: inter-character;
        text-align:justify; 
        text-justify:inter-ideograph;
        text-indent: 2em;
        line-height: 40px;
        font-size: 16px;
        //font-family: '微软雅黑';
      }
    }
  }
}
</style>


