<template>
  <div class="about-container">
    <div style="width:100px;height:100px;"></div>
    <div class="about-content">
      <div style="height:30px;"></div>
      <div class="main-title">
        队 伍 介 绍
      </div>
      <div class="info">
        <el-row>
          <div v-for="(item,index) in infoList" :key="index">
            <el-col style="width:260px;" :class="{'move':index<3}">
              <div class="info-card" :body-style="{ padding: '0px' }">
                <img :src="item.head_image" class="image">
                <div style="padding: 14px;height:150px;">
                  <div class="title">{{ item.name }}</div>
                  <div class="bottom clearfix">
                    <p v-for="val in item.info" :key="val">
                      <i class="el-icon-info"></i><span style="margin-left:10px;line-height:30px;">{{ val }}</span>
                    </p>
                  </div>
                </div>
              </div>
            </el-col>
          </div >
        </el-row>
      </div>
      <div style="height:50px;"></div>
      <div class="main-title">
        联 系 我 们
      </div>
      <div class="contact-us">
        <div class="contact-us-item" style="margin-left:365px;">
          <img :src="github_main">
          <div style="margin-top:10px;">
            GitHub主页
          </div>
        </div>
        <div class="contact-us-item" style="margin-left:70px;">
          <img :src="github_issues">
          <div style="margin-top:10px;">
            反馈问题
          </div>
        </div>
        <div class="phone">
          <p 
            @mouseenter="isLink = true" 
            @mouseleave="isLink = false">
              GitHub地址:  
              <el-button :class="{'link-hover':isLink}" type="text" @click="linkGitHub">
                https://github.com/q1271964185/Reaper
              </el-button>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import head_image3 from '../assets/head_image3.jpg'
import head_image1 from '../assets/head_image1.jpg'
import head_image2 from '../assets/head_image2.jpg'
import head_image4 from '../assets/head_image4.jpg'
import github_main from '../assets/github_main.png'
import github_issues from '../assets/github_issues.png'
export default {
  data(){
    return {
     head_image3:head_image3,
     head_image1:head_image1,
     head_image2:head_image2,
     head_image4:head_image4,
     github_main:github_main,
     github_issues:github_issues,
     isLink:false,
     infoList:[
                {name:'Sp4rkW',head_image:head_image1,info:['安全圈子爱好者']},
                {name:'安逸猪',head_image:head_image2,info:['佛性少年','充满好奇心却又缺少行动力']},
                {name:'打代码要优雅',head_image:head_image3,info:['有活力的码农','热爱生活']},
                {name:'我不会翻转二叉树',head_image:head_image4,info:['可以不美丽但要善良','可以不完满但要努力']}
              ]
    }
  },
  mounted(){
    
  },
  watch:{
  },
  methods:{
   linkGitHub(){
     window.open('https://github.com/q1271964185/Reaper') 
   }
  }
}
</script>

<style lang="scss">
.about-container{
  .el-card__body{
    float: left;
  }
  .el-button--text{
    color: black;
    font-size: 16px;
  }
}

</style>

<style lang="scss" scoped>
.about-container{
  font-family: "PingFang SC","微软雅黑",Arial,sans-serif;
  margin: 0 auto;
  padding: 0;
  width: 1200px;
  background-color: #fff;
  .link-hover{
    color: #409EFF;
  }
  .about-content{
    width: 100%;
    img{
      width:100%;
      height:260px;
    }
    .main-title{
      font-size: 30px;
      margin: 0 auto;
      //background-color: rgb(15, 172, 219);
      width: 180px;
      height: 70px;
      line-height: 70px;
      color: rgb(66, 62, 62);
      text-align: center;
      border-bottom: solid 2px rgb(95, 93, 93);
      //box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 15px 0 rgba(0, 0, 0, 0.19);
      //border-radius: 0px 35px 35px 0px;
      //text-shadow:5px 5px 10px rgb(192, 185, 185);
    }
    .info{
      margin-bottom: 20px;
      margin-top: 50px;
      .move{
        margin-right:53px;
      }
      .title{
        font-size: 20px;
        height: 60px;
        line-height: 60px;
        text-align: center;
        font-weight: bold;
      }
      .info-card{
        box-shadow: 0 0px 0px 0 rgba(0, 0, 0, 0.2), 0 0px 3px 0 rgba(0, 0, 0, 0.19);
      }
      p{
        font-size: 16px;
      }
    }
    .contact-us {
      width: 100%;
      margin-top: 40px;
      margin-bottom: 30px;
      text-align: center;
      //box-shadow: 0 0px 0px 0 rgba(0, 0, 0, 0.2), 0 0px 3px 0 rgba(0, 0, 0, 0.19);
      .contact-us-item{
        margin: 0 auto;
        float: left;
        width: 200px;
        height: 280px;
        font-size: 16px;
        img{
          box-shadow: 0 0px 0px 0 rgba(0, 0, 0, 0.2), 0 0px 3px 0 rgba(0, 0, 0, 0.19);
          width: 200px;
          height: 200px;
        }
      }
      .phone{
        clear: both;
        font-size: 16px;
        cursor: pointer;
        p{
          line-height: 30px;
        }
      }
    }
  }
}
</style>


